.container {
  font-family: "Rubik", "Arial", sans-serif;
  box-sizing: border-box;
  width: 1568px;
  margin: 0 auto;
  padding: 80px 86px;
}
@media (max-width: 1559px) {
  .container {
    width: 1214px;
  }
}
@media (max-width: 1279px) {
  .container {
    padding: 60px 40px;
    width: 768px;
  }
}
@media (max-width: 767px) {
  .container {
    width: 320px;
    padding: 40px 40px 40px 20px;
  }
}

@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Rubik";
  font-display: swap;
  src: url("../fonts/rubik-300.woff2") format("woff2"), url("../fonts/rubik-300.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Rubik";
  font-display: swap;
  src: url("../fonts/rubik-400.woff2") format("woff2"), url("../fonts/rubik-400.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Rubik";
  font-display: swap;
  src: url("../fonts/rubik-700.woff2") format("woff2"), url("../fonts/rubik-700.woff") format("woff");
}
.main-index {
  overflow-x: hidden;
  min-width: 320px;
  width: 100%;
  background-color: #ffffff;
}

.slider {
  background-color: #ffffff;
}

.slider__container {
  position: relative;
  z-index: 1;
}

.slider__text {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
  max-width: 100%;
  flex-wrap: wrap;
  column-gap: 25px;
  margin-bottom: 40px;
}
@media (max-width: 767px) {
  .slider__text {
    margin-bottom: 25px;
  }
}

.slider__text h2 {
  margin: 0;
  font-weight: 700;
  font-size: 34px;
  line-height: 40px;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .slider__text h2 {
    font-size: 18px;
    line-height: 26px;
  }
}

.slider__text a {
  transition: 0.3s;
  font-weight: 400;
  font-size: 24px;
  line-height: 34px;
  color: #535658;
  text-decoration: none;
  border-bottom: 1px dashed #535658;
}
@media (max-width: 767px) {
  .slider__text a {
    margin-top: 6px;
    font-size: 14px;
    line-height: 22px;
  }
}

.slider__text a:hover {
  text-shadow: #f1d264 0 0 5px;
}

.slider__text a:active {
  color: #f1d264;
}

.slider__buttons {
  z-index: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
@media (max-width: 767px) {
  .slider__buttons {
    justify-content: flex-end;
  }
}

.slider__button {
  transition: 0.3s;
  cursor: pointer;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 77px;
  width: 40px;
  height: 40px;
  background-color: transparent;
  border: 1px solid #ecf0f4;
  border-radius: 5px;
}
@media (max-width: 767px) {
  .slider__button {
    display: none;
  }
}

.slider__button:hover {
  box-shadow: #f1d264 0 0 5px;
}

.slider__button:active {
  border-color: #f1d264;
  background-color: #f1d264;
}

.slider__inner {
  margin: 0 auto;
  width: 100%;
}

.slider__list {
  width: 100%;
  box-sizing: border-box;
  min-height: 496px;
  height: fit-content;
}
@media (max-width: 767px) {
  .slider__list {
    min-height: 441px;
  }
}

.slider__item {
  width: 334px;
  height: 100%;
  padding-bottom: 25px;
  position: relative;
  border: 1px solid #ecf0f4;
  border-radius: 5px;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  box-sizing: border-box;
}

.slider__item--hit::after {
  position: absolute;
  content: "";
  background-image: url("../img/svg/hit.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 50px;
  height: 50px;
  top: 20px;
  left: 20px;
}

.slider__item--new::after {
  position: absolute;
  content: "";
  background-image: url("../img/svg/new.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 50px;
  height: 50px;
  top: 20px;
  left: 20px;
}

.slider__item-buttons {
  z-index: 2;
  width: 34px;
  display: flex;
  flex-direction: column;
  row-gap: 5px;
  position: absolute;
  top: 20px;
  right: 20px;
}

.slider__item-button {
  cursor: pointer;
  transition: 0.3s;
  background-color: #ffffff;
  border: none;
  padding: none;
  width: 34px;
  height: 34px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50px;
}

.slider__item-button svg {
  width: 20px;
}

.slider__item-button:hover {
  background-color: #f1d264;
  box-shadow: #f1d264 0 0 5px;
}

.slider__imgs-inner {
  position: relative;
  height: 334px;
}
@media (max-width: 767px) {
  .slider__imgs-inner {
    height: 260px;
  }
}

.slider__tabs {
  position: absolute;
  bottom: 12px;
  left: 50%;
  transform: translate(-50%, 0);
}

.slider__tab {
  background-color: rgba(0, 0, 0, 0.2);
  padding: 0;
  border: 0;
  height: 2px;
  width: 20px;
  cursor: pointer;
}

.slider__tab--act {
  background-color: #f1d264;
}

.slider__img-wrapper {
  display: none;
  width: 100%;
  height: 334px;
}
@media (max-width: 767px) {
  .slider__img-wrapper {
    height: 260px;
  }
}

.slider__img-wrapper--act {
  display: block;
}

.slider__img {
  background-color: #f5f5f5;
  object-fit: cover;
  height: 100%;
  width: 100%;
}

.slider__descrip {
  margin: 10px 25px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  min-height: 67px;
}

.slider__descrip p {
  text-align: center;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  margin: 0;
}
@media (max-width: 767px) {
  .slider__descrip p {
    font-size: 16px;
    line-height: 22px;
    margin-bottom: 15px;
  }
}

.slider__descrip b {
  font-weight: 700;
  font-size: 20px;
  line-height: 22px;
  margin: auto 0 0;
  padding: 0;
}

.slider__descrip span {
  width: 41px;
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  text-decoration-line: line-through;
  color: #8d9295;
  margin: auto 0 0;
  margin-left: 15px;
}
@media (max-width: 767px) {
  .slider__descrip span {
    margin-left: 17px;
  }
}

.slider__add {
  display: flex;
  justify-content: space-between;
  column-gap: 10px;
}

.slider__counts {
  width: 100px;
  height: 48px;
  border: 1px solid #ecf0f4;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 767px) {
  .slider__counts {
    display: none;
  }
}

.slider__count-add {
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
  color: #4c4e50;
  width: 30px;
  text-align: center;
}

.slider__count {
  transition: 0.3s;
  border: none;
  background-color: transparent;
  cursor: pointer;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.slider__count:hover {
  background-color: #f1d264;
  box-shadow: #f1d264 0 0 5px;
}

.slider__count:active {
  background-color: #ffffff;
}

.slider__cart {
  color: #000000;
  transition: 0.3s;
  cursor: pointer;
  font-family: "Rubik", "Arial", sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  width: 174px;
  height: 50px;
  border: none;
  background-color: #f1d264;
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;
}
@media (max-width: 767px) {
  .slider__cart {
    width: 220px;
    margin-top: 10px;
  }
}

.slider__cart:hover {
  box-shadow: #f1d264 0 0 10px;
}

.slider__cart:active {
  background-color: #ffffff;
}

.slider__cart svg {
  margin-right: 12px;
}